<template>
    <div class="container">
      <newItem></newItem>
      <Loading
        v-bind:show="loading.flag"
      ></Loading>


<div class="ingItem">
      <packageList
       v-for="item in packageData[0]"
      :id = "item.Id"
      :price = "item.price"
      :location = "item.position"
      :gender = "item.sexual"
      :size = "item.size"
      :remark = "item.remark"
      ></packageList>
    </div>
      <div class="weui-loadmore" v-show = "scrollFlag">
    <i class="weui-loading"></i>
    <span class="weui-loadmore__tips">正在加载</span>
</div>
      <div class="weui-footer" v-show = "noMoreData" >
    <p class="weui-footer__text">没有更多数据啦~</p>
    <!-- 未完成列表结束 -->
</div>
<div class="ing">
<div class="empty" v-show="empty">
  <div class="fire">
    <svg width="100%" height="100%" viewBox="0 0 890 1025" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
        <!-- Generator: Sketch 41 (35326) - http://www.bohemiancoding.com/sketch -->
        <title>火</title>
        <desc>Created with Sketch.</desc>
        <defs>
            <linearGradient x1="50%" y1="0%" x2="50%" y2="100%" id="linearGradient-1">
                <stop stop-color="#FFFFFF" stop-opacity="0" offset="0%"></stop>
                <stop stop-color="#000000" stop-opacity="0.434358016" offset="100%"></stop>
            </linearGradient>
            <path d="M271.536,724.968 C328.976,641 343.792,557.896 343.792,557.896 C343.792,557.896 388.944,616.584 370.896,708.392 C450.672,619.592 465.712,478.12 453.68,423.944 C634,549.96 711.056,822.792 607.216,1025 C1159.6,712.456 744.624,244.84 672.368,192.136 C696.464,244.808 701.008,333.992 652.368,377.256 C570.032,64.968 366.384,0.968 366.384,0.968 C390.48,162.024 279.088,338.12 171.696,469.704 C167.92,405.48 163.92,361.16 130.16,299.72 C122.576,416.392 33.424,511.464 9.296,628.36 C-23.408,786.632 33.776,902.536 251.056,1024.968 C182.736,882.984 219.088,801.608 271.536,724.968 Z" id="path-2"></path>
        </defs>
        <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
            <g id="Shape">
                <use fill="url(#linearGradient-1)" style="mix-blend-mode: soft-light;" xlink:href="#path-2"></use>
                <use fill="#777777" xlink:href="#path-2"></use>
            </g>
        </g>
    </svg>

  </div>
  <h4>太火啦，订单都被抢光了！</h4>
</div>
  </div>
  <div class="endOrder">


  <div class="weui-cells__title">最新完成订单</div>

<div class="endOrderItem" v-for="item in packageData[1]">
  <finishListItem
  :id = "item.Id"
  :price = "item.price"
  :location = "item.position"
  :gender = "item.sexual"
  :size = "item.size"
  :remark = "item.remark"
  ></finishListItem>
  <img src="../../assets/end2.png" alt=""  />
</div>

  </div>
    </div>
</template>
<script>
import userAPI from 'components/mixins/api/user.vue'
import packageList from './package-list.vue'
import finishListItem from './finishListItem.vue'
import newItem from './new-item.vue'
import Loading from 'components/loading'
import toastMixin from 'components/mixins/toast'
import orderAPI from 'components/mixins/api/order'
import $ from 'webpack-zepto'
export default{
  name: 'index',
  mixins: [toastMixin, orderAPI, userAPI],
  data: () => {
    return {
      empty: false,   // 仅当第一页数据也为空时
      packageData: [],
      scrollFlag: false,
      noMoreData: false,
      page: 1
    }
  },
  created: function () {
    this.getData(this.page)
    /* eslint-disable*/
    // var Iscroll = require('iscroll-component');
    // var el = document.getElementById('scroll');
    // var scroll = new Iscroll(el);
    // el.addEventListener('scroll', function(e) {
    //   console.log(el.scrollTop);
    // }, false);
    /* eslint-enable */
  },
  mounted: function () {
    // $('.container').off('transitionend').on('transitionend', () => {
    //   $(window).on('scroll', (e) => {
    //     console.log('on')
    //     this.scrollLoad()
    //   })
    // })      如果不是从其他页面进入的就会不会触发这个事件
    let that = this
    setTimeout(() => {
      console.log('on')
      $(window).on('scroll', (e) => {
        that.scrollLoad()
      })
    }, 800)
  },
  beforeDestroy: function () {
    console.log('off')
    $(window).off('scroll', this.scrollLoad)
  },
  methods: {
    getData: function (page) {
      let that = this
      this.getAll(page, (err, data) => {
        if (err) {
          that.showToast('error')
        } else {
          // TODO 加载的视觉效果
          // data = JSON.parse(data)
          if (data[0].length === 0 && that.page === 1) {
            that.empty = true
          } else if (data[0].length === 0) {
            that.noMoreData = true
          }
          that.packageData = that.packageData.concat(data)
        }
        this.scrollFlag = false
      })
    },
    scrollLoad: function () {
      if (this.scrollFlag || this.noMoreData) {
        return
      }
      let body = document.body
      // let height = document.documentElement.clientHeight + body.scrollTop // 可视窗口大小 + 可视区域距离元素上头的距离
      // let scrollHeight = body.scrollHeight    // 整个页面的大小
      let height = $('.ingItem').height()   // 获取列表的高度
      let scrollHeight = body.scrollTop  // 滚动了的距离
      // console.log(height + ' ' + scrollHeight)
      if (height < scrollHeight + 40) {     // 滚动了的距离快等于列表的长度
        console.log('trigger!!!')
        this.scrollFlag = true
        // 如果没有被占用则请求下一页
        this.page++
        this.getData(this.page)
      }
    }
  },
  components: {
    packageList, newItem, Loading, finishListItem
  }
}

</script>
<style lang="scss">
.endOrder{
  .endOrderItem{
    position: relative;
    img {
      position: absolute;
      width: auto;
      height: 60%;
      top: 20%;
      right: 4rem;
    }
  }
}

.ing {
  position: relative;
  height: 300px;
  width: 100%;
}
body{
  background-color:#eee;
  margin-bottom:80px;
}
.weui-footer__text{
  margin-top:15px;
}
.next{
  opacity:0.8;
}


.empty{
  position: absolute;
  top: 40%;
  opacity: 1;
  // transition-delay: .5s;
  transition: opacity .5s ease;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  .fire{
    width: 10rem;
    margin: 0 auto;
  }
  h4{
    font-size: 1.5rem;
    font-weight: lighter;
  }
}
.ios9 .empty{
  transition-delay: .5s!important;
}

</style>



